// Modules //
import React from 'react';
import axios from 'axios';
import { BrowserRouter as Router, Switch, Route, Link, Redirect } from "react-router-dom";
import jsPDF from 'jspdf';
import autoTable from 'jspdf-autotable';

// Style //
import './VosCommandes.css';
import { AiFillDelete, AiTwotoneSetting, AiTwotoneEye } from 'react-icons/ai';
import { Button, Container, Row, Col } from 'react-bootstrap';

class VosCommandes extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      allCommandes: [],
    }
  }

  componentDidMount() {
    axios.get("http://ec2-18-218-191-39.us-east-2.compute.amazonaws.com/commandes_clients/user/" + localStorage.token).then((res) => {
      console.log(res.data.commandes);

      this.setState({ allCommandes: res.data.commandes })
    })
  }

  pdf = value => () => {
    var imgData = 'data:image/png;base64,';
    let pdf = new jsPDF('p', 'pt');

    pdf.addImage(imgData, 'PNG', 15, 20, 120, 120);

    pdf.autoTable({ margin: { top: 200 }, html: '#detail-facture' })
    pdf.text(120, 60, 'DYMED EU S.A.R.L')
    pdf.text(120, 80, '42 Boulevard du Campus')
    pdf.text(120, 100, '42420 Parfait-sur-Oise')
    pdf.text(120, 120, 'France')
    pdf.text(120, 140, 'TVA: FR123470364957')

    pdf.autoTable({
      head: [['Produits', 'Quantité', 'Frais de Port', 'Prix']],
      body: [
        [this.state.allCommandes[value].caracteristiques_produits.articles.titre,
          this.state.allCommandes[value].quantity,
          this.state.allCommandes[value].prix_port,
          this.state.allCommandes[value].prix_articles],
      ],
    })

    pdf.text(50, 280, 'Adresse mail du client: ' + this.state.allCommandes[value].email)
    pdf.text(50, 300, 'Date de la commande: ' + this.state.allCommandes[value].reception_at)

    pdf.save('facture.pdf')
  }

  render() {
    if (this.props.isUser === true || this.props.isAdmin === true) {
      return (
        <div className="admin-categorie">
          <center>
            <Container>
              <Col>
                <h3 className="commandes-client">Vos Commandes </h3><br />
                <table className="admin-table_categories">
                  <tbody>
                    <tr className="admin-table_tr">
                      <th>Photo</th>
                      <th>Produits</th>
                      <th>Quantités</th>
                      <th>Mode de Livraison</th>
                      <th>Statut</th>
                      <th>Frais de Port</th>
                      <th>Prix des Articles</th>
                      <th>Facture</th>
                    </tr>
                    {this.state.allCommandes.map((item, i) => {
                      return (
                        <tr key={i}>
                          <td className="admin-table_td"><img style={{ width: "80px" }} src={item.caracteristiques_produits.articles.photo} /></td>
                          <td className="admin-table_td">{item.caracteristiques_produits.articles.titre} - {item.caracteristiques_produits.format}</td>
                          <td className="admin-table_td">x{item.quantity}</td>
                          <td className="admin-table_td">{item.expeditions.transporteurs.nom} - {item.expeditions.nom}</td>
                          <td className="admin-table_td">{item.status}</td>
                          <td className="admin-table_td">{item.prix_port} €</td>
                          <td className="admin-table_td">{item.prix_articles} €</td>
                          <td className="admin-table_td"><button className="btn-facture" onClick={this.pdf(i)}>Facture</button></td>
                        </tr>
                      )
                    })}
                  </tbody>
                </table>
              </Col>
            </Container>
          </center>
        </div>
      );
    } else {
      return <Redirect to="/" />
    }
  }
}

export default VosCommandes;
